

.container{
    background: linear-gradient(109.6deg, rgb(27, 27, 79) 11.2%, rgb(7, 136, 123) 100.2%);
    height: 550px;
    width: 480px;
    position: relative;
    left: 35rem;
    padding: 2rem 4rem;
    margin-top: 3rem;
}

.container form{
    width: 80%;
    background-color: #fff;
    padding: 0.5rem 2rem;
    border-radius: 0.5rem;
    margin-top: 1rem;
    text-align: center;
    align-items: center;
}

form input{
    width: 100%;
    border: none;
    background-color: transparent;
    font-size: 1rem;
    font-weight: 600;
}

form input:focus{
    outline: none;
}

h2, h3,h4, p{
    color: #fff;
}

.date{
    font-size: 1.5rem;
    font-weight: 100;
    text-align: center;
}

.textError{
    color: red;
}

/* p{
    display: inline-block;

} */

.weatherLocation{
    font-size: 1.3rem;
    font-weight: 100;
    text-align: center;
    margin-top: 0.6rem;
}

.container table{
    margin-top: 1rem;
}

table tr{
    display: flex;
    justify-content: space-between;
    gap: 17.6rem;
    text-align: left;
    margin-bottom: 1.5rem;
    color: #fff;
}


tr td{
    text-align: right;
}

hr{
    border: none;
    border-bottom: 1px solid #fff;
}

.weather-content{
    display: flex;
    justify-content: space-between;
}

.weather-content img{
    width: 170px;
    height: 220px;
}

.weather-content .weather-texts{
    font-size: 1.5rem;
    margin-top: 1.8rem;
}